<template>
  <div class="form">
    <div class="form__background"></div>
    <form class="form__content">
      <h3 class="form__title">Анкета</h3>
      <input
        v-model="nameFirst"
        placeholder="Имя и Фамилия"
        type="text"
        class="form__input"
      />
      <div
        v-if="!showInputSecondPerson"
        @click.prevent="showInputSecondPerson = !showInputSecondPerson"
        class="form__add"
      >
        <div class="form__circle">
          <img :src="srcImagePlus" alt="icon add" />
        </div>
        <span class="form__text">Добавить гостя</span>
      </div>

      <input
        v-else
        v-model="nameSecond"
        placeholder="Имя и Фамилия второго гостя"
        type="text"
        class="form__input"
      />

      <div class="form__comment">
        <span class="form__text">Комментарий</span>
        <v-textarea v-model="comment" solo dense no-resize color="#2D3A99">
        </v-textarea>
      </div>

      <v-radio-group
        v-model="selectedPresence"
        mandatory
        class="form__group-radio"
      >
        <v-radio
          v-for="item in presence"
          :key="item.name"
          :label="item.name"
          color="white"
          :value="item"
          class="form__radio"
        ></v-radio>
      </v-radio-group>

      <!--
      <div v-for="item in presence" :key="item.name" class="form__add">
        <input
          type="radio"
          :value="item"
          v-model="selectedPresence"
          class="form__circle"
        />
        <label class="form__text">{{ item.name }}</label>
      </div>
      -->

      <!--
      <div @click.prevent="presence = true" class="form__add">
        <div class="form__circle"></div>
        <span class="form__text">Подтвердить присутствие</span>
      </div>
      <div @click.prevent="absence = true" class="form__add">
        <div class="form__circle"></div>
        <span class="form__text">Я не смогу</span>
      </div>

      -->

      <button @click.prevent="updateBD()" class="form__button">
        Отправить
      </button>
    </form>
    <PopupError :dialog.sync="dialogError" :text.sync="messageError" />
    <PopupSuccess :dialog.sync="dialogSuccess" />
  </div>
</template>

<script>
import imagePlus from "../assets/images/plus.svg";

export default {
  name: "Form",
  components: {
    PopupError: () => import("@/components/PopupError"),
    PopupSuccess: () => import("@/components/PopupSuccess"),
  },
  data: () => {
    return {
      nameFirst: null,
      showInputSecondPerson: false,
      nameSecond: null,
      comment: null,
      // rules: [(val) => (val || "").length > 0 || "Обязательно заполните"],
      // presence: false,
      // absence: false,
      // rules: [
      //   (value) => !!value || "Required.",
      //   (value) => (value && value.length >= 3) || "Min 3 characters",
      // ],
      srcImagePlus: imagePlus,

      presence: [
        { name: "Подтвердить присутствие", status: false },
        { name: "Я не смогу", status: false },
      ],
      selectedPresence: null,
      dialogError: false,
      messageError: null,
      dialogSuccess: false,
    };
  },
  methods: {
    async updateBD() {
      console.log("click form");

      const urlBD =
        "https://wedding0505-75f39-default-rtdb.firebaseio.com/list/guests.json";

      let presence = false;

      if (this.selectedPresence.name === this.presence[0].name) presence = true;

      if (this.nameFirst === null || this.nameFirst === "") {
        this.messageError =
          "Заполните ваше Имя и Фамилию перед отправкой анкеты";
        this.dialogError = true;
      }

      const parametr = {
        nameFirst: this.nameFirst === "" ? null : this.nameFirst,
        nameSecond: this.nameSecond === "" ? null : this.nameSecond,
        comment: this.comment === "" ? null : this.comment,
        presence: presence,
      };

      console.log(parametr);
      console.log(JSON.stringify(parametr));

      await fetch(urlBD, {
        method: "POST",
        body: JSON.stringify(parametr),
        headers: { "content-type": "application/x-www-form-urlencoded" },
      })
        .then((response) => {
          if (response.status !== 200) {
            console.log(`Ошибка сохранения. Статус ошибки: ${response.status}`);
            return Promise.reject();
          }
          return response.json();
        })
        .then((result) => {
          this.dialogSuccess = true;
          console.log("result", result);
        })
        .catch((error) => {
          console.log("Ошибка", error);
        });
    },
  },
};
</script>

<style lang="scss">
@import "../assets/scss/main.scss";

.form {
  //   position: relative;
  //   display: flex;
  //   flex-direction: column;
  //
  position: relative;
  border-radius: $border-radius;
  box-shadow: $box-shadow-form;

  &__background {
    background: $black-opacity;
    border-radius: $border-radius;
    filter: blur(0.5px);
    -webkit-filter: blur(0.5px);
    backdrop-filter: blur(5px);
    width: 660px;
    height: 830px;

    @include for-size(big-desktop) {
      width: 850px;
      height: 1000px;
    }

    @include for-size(desktop) {
      width: 560px;
      height: 740px;
    }

    @include for-size(big-mobile) {
      width: 420px;
      height: 620px;
    }

    @include for-size(mobile) {
      width: 310px;
      height: 540px;
    }
  }

  &__content {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 80px;

    @include for-size(big-desktop) {
      padding: 100px 120px;
    }

    @include for-size(big-mobile) {
      padding: 50px;
    }

    @include for-size(mobile) {
      padding: 20px 15px;
    }
  }

  &__title {
    @include main-name();
    // text-align: center;
  }

  &__input {
    margin-top: 50px;
    padding: 10px 0 10px 10px;
    // padding-left: 10px;
    // padding-bottom: 10px;
    width: 500px;
    border-bottom: $border-white;
    outline: none;
    @include text-form();
    transition: all 0.5s;

    &::placeholder {
      @include text-form();
    }

    &:focus {
      box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
      // background: rgba(255, 255, 255, 0.1);
      border-radius: 15px 15px 0 0;
      border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    }

    @include for-size(big-desktop) {
      width: 600px;
      margin-top: 60px;
    }

    @include for-size(desktop) {
      margin-top: 40px;
      width: 400px;
    }

    @include for-size(tablet) {
      border-bottom: 1px solid $white;
    }

    @include for-size(big-mobile) {
      margin-top: 30px;
      width: 300px;
    }

    @include for-size(mobile) {
      width: 280px;
      // margin-top: 20px;
    }
  }

  &__add {
    display: flex;
    align-items: center;
    margin-top: 50px;
    width: 100%;

    // & input[type="radio"]:checked + label:before {
    //   background: red;
    // }

    @include for-size(big-desktop) {
      margin-top: 60px;
    }

    @include for-size(desktop) {
      margin-top: 40px;
    }

    @include for-size(big-mobile) {
      margin-top: 30px;
    }

    @include for-size(mobile) {
      // margin-top: 20px;
    }
  }

  &__circle {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    width: 25px;
    height: 25px;
    border-radius: 25px;
    border: $border-white;

    img {
      width: 60%;
    }

    @include for-size(tablet) {
      border: 1px solid $white;

      width: 20px;
      height: 20px;
    }

    @include for-size(mobile) {
      width: 16px;
      height: 16px;
    }
  }

  &__text {
    @include text-form();
  }

  &__comment {
    margin-top: 50px;
    width: 100%;

    @include for-size(big-desktop) {
      margin-top: 60px;
    }

    @include for-size(desktop) {
      margin-top: 40px;
    }

    @include for-size(big-mobile) {
      margin-top: 30px;
    }

    @include for-size(mobile) {
      // margin-top: 20px;
    }
  }

  &__textarea {
    margin-top: 10px;
    height: 90px;
    border-radius: $border-radius;
    border: $border-white;

    @include for-size(tablet) {
      border: 1px solid $white;
    }

    // & > .v-input__control > .v-input__slot {
    //   // padding: 10px;
    //   background: transparent;
    // }

    // &
    //   .v-text-field.v-text-field--enclosed:not(.v-text-field--rounded)
    //   > .v-input__control
    //   > .v-input__slot {
    //   padding: 10px;
    // }

    // .v-input__slot {
    //   padding: 10px;
    // }
  }

  &__group-radio {
    width: 100%;
  }

  &__radio {
    margin-top: 50px;

    @include for-size(big-desktop) {
      margin-top: 60px;
    }

    @include for-size(desktop) {
      margin-top: 40px;
    }

    @include for-size(big-mobile) {
      margin-top: 30px;
    }

    .v-icon {
      color: $white !important;

      @include for-size(tablet) {
        font-size: 20px;
      }

      @include for-size(mobile) {
        font-size: 16px;
      }
    }

    .v-label {
      margin-left: 20px;
      @include text-form();
      color: $white !important;
      letter-spacing: 0.1em !important;

      @include for-size(mobile) {
        margin-left: 0;
      }
    }

    @include for-size(big-desktop) {
      margin-top: 60px;
    }

    @include for-size(desktop) {
      margin-top: 40px;
    }

    @include for-size(big-mobile) {
      margin-top: 30px;
    }

    @include for-size(mobile) {
      // margin-top: 20px;
    }
  }

  &__button {
    margin-top: 60px;
    padding: 10px 35px;
    border-radius: $border-radius;
    border: $border-white;
    @include text-form();
    transition: all 0.5s;

    @include for-size(big-desktop) {
      margin-top: 80px;
    }

    @include for-size(desktop) {
      margin-top: 50px;
    }

    @include for-size(tablet) {
      border: 1px solid $white;
    }

    @include for-size(big-mobile) {
      margin-top: 40px;
    }

    @include for-size(mobile) {
      // margin-top: 20px;
    }

    &:hover {
      box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
      transform: scale(1.05);
    }
  }

  .v-input--selection-controls {
    margin-top: 0;
    padding-top: 0;
  }

  .v-messages {
    display: none;
  }

  .v-input__slot {
    margin-bottom: 0;
  }

  .v-input--radio-group--column .v-radio:not(:last-child):not(:only-child) {
    margin-bottom: 0;
  }

  .v-text-field__details {
    display: none;
  }

  .v-text-field.v-text-field--solo:not(.v-text-field--solo-flat) {
    & > .v-input__control > .v-input__slot {
      margin: 10px 0 0;
      padding: 0;
      height: 100px;
      box-shadow: none;
      border: 1px solid $white;
      border-radius: $border-radius;
      background: transparent;
      transition: all 0.5s;

      .v-text-field__slot {
        margin: 0;
        padding: 10px;

        & > textarea {
          margin-top: 0;
          padding-right: 5px;

          @include scroll(6px);
          @include text-form();
        }
      }
    }

    &.v-input--is-focused > .v-input__control > .v-input__slot {
      border: 1px solid rgba(255, 255, 255, 0.5);
      box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
    }
  }
}
</style>
