import Vue from "vue";
import App from "./App.vue";
import vuetify from "@/plugins/vuetify";
import VueMaterial from "vue-material";
import "vue-material/dist/vue-material.min.css";
import "vue-material/dist/theme/default.css";
import {
    initializeApp
} from "firebase/app";
import {
    getFirestore
} from "firebase/firestore";

Vue.use(VueMaterial);

// ..........................................................
import {
    FontAwesomeIcon
} from "@fortawesome/vue-fontawesome";

Vue.component("font-awesome-icon", FontAwesomeIcon);
// ..........................................................
Vue.config.productionTip = false;

const firebaseConfig = {
    apiKey: process.env.VUE_APP_KEY,
    authDomain: process.env.VUE_APP_DOMAIN,
    projectId: process.env.VUE_APP_ID,
    storageBucket: process.env.VUE_APP_BUCKET,
    messagingSenderId: process.env.VUE_APP_SENDERID,
    appId: process.env.VUE_APP_APPID,
    measurementId: process.env.VUE_APP_MEASUREMENT
};

const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

export {
    db
};

new Vue({
    vuetify,
    render: (h) => h(App),
}).$mount("#app");